import { fetchEndpoint } from '../server/network'

export const getBiddingList = (params, page, pageSize = 10) =>
  fetchEndpoint('/admin/api.auction.list', 'POST', {
    page: page,
    size: pageSize,
    goods_sn: params.goodId,
    goods_name: params.goodName,
    min_price: params.dealMinMoney,
    max_price: params.dealMaxMoney,
    min_start: params.sellMinMoney,
	max_start: params.sellMaxMoney,
	status: params.status
  })

export const getEnrollList = (id, params, page, pageSize = 10) =>
  fetchEndpoint('/admin/api.sing.list', 'POST', {
    page: page,
    size: pageSize,
    paipai_id: id,
    user_id: params.userID,
    phone: params.userTel,
    start_time: Number(params.startTime / 1000),
    end_time: Number(params.endTime / 1000),
    status: params.status
  })

export const getOfferList = (id, params, page, pageSize = 10) =>
  fetchEndpoint('/admin/api.bidding.list', 'POST', {
    page: page,
    size: pageSize,
    paipai_id: id,
    user_id: params.userID,
    phone: params.userTel,
    min_price: params.offerMinMoney,
    max_price: params.offerMaxMoney,
    start_time: Number(params.startTime / 1000),
    end_time: Number(params.endTime / 1000)
  })
