<template>
  <div class="container">
    <page-title first-title="竞拍记录"></page-title>
    <!-- search -->
    <div class="bid-unique-search-wrapper">
      <el-form :inline="true">
        <el-form-item label="商品名称：" :label-width="width">
          <el-input v-model="params.goodName" placeholder="请输入商品名称" clearable></el-input>
        </el-form-item>
        <el-form-item label="商品货号：" :label-width="width">
          <el-input v-model="params.goodId" placeholder="请输入商品货号" clearable></el-input>
        </el-form-item>
        <br />
        <el-form-item label="成交价：" :label-width="width">
          <el-input v-model="params.dealMinMoney" clearable></el-input>
        </el-form-item>
        <el-form-item label="~" :label-width="width">
          <el-input v-model="params.dealMaxMoney" clearable></el-input>
        </el-form-item>
        <br />
        <el-form-item label="起拍价：" :label-width="width">
          <el-input v-model="params.sellMinMoney" clearable></el-input>
        </el-form-item>
        <el-form-item label="~" :label-width="width">
          <el-input v-model="params.sellMaxMoney" clearable></el-input>
        </el-form-item>
        <br />
        <el-form-item label="竞拍状态：" :label-width="width">
          <el-select v-model="params.status" clearable>
            <el-option v-for="item in status" :key="item.id" :label="item.name" :value="item.id"></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <el-button type="primary" @click="searchBid">搜索</el-button>
      <el-button type="primary" @click="clearParams">清空</el-button>
    </div>
    <!-- table -->
    <div class="list-wrapper">
      <el-table
        :data="biddingData"
        style="width: 100%"
        border
        :header-cell-style="{ background: '#eef1f6' }"
        v-loading="loading"
      >
        <el-table-column label="商品名称" prop="name" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column label="货号" prop="sn" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column label="起拍价" prop="starting_price"></el-table-column>
        <el-table-column label="保证金" prop="deposit"></el-table-column>
        <el-table-column label="加价幅度" prop="range"></el-table-column>
        <el-table-column label="当前价/成交价">
          <template v-slot="scope">
            <span>{{ scope.row.current_price }}</span
            >/
            <span>{{ scope.row.closing_cost }}</span>
          </template>
        </el-table-column>
        <el-table-column label="浏览量" prop="uv"></el-table-column>
        <el-table-column label="报名人数" prop="sing_number"></el-table-column>
        <el-table-column label="出价次数" prop="bid_number"></el-table-column>
        <el-table-column label="竞拍状态" prop="goods_status" :formatter="fmtStatus"></el-table-column>
        <el-table-column label="操作" width="240px">
          <template v-slot="scope">
            <el-button type="text" @click="goEnroll(scope.row.id)">查看报名详情</el-button>
            <el-button type="text" @click="goOffer(scope.row.id)">查看出价详情</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <!-- pagination -->
    <div class="list-pagination-wrapper">
      <el-pagination
        @current-change="handleCurrentChange"
        :current-page.sync="currentPage"
        background
        layout="prev, pager, next, jumper"
        :total="total"
        :hide-on-single-page="true"
      >
      </el-pagination>
    </div>
  </div>
</template>

<script>
import { getBiddingList } from '../../api/bidding'
import { PP_STATUS } from './static'
export default {
  name: 'BiddingRecord',
  data() {
    return {
      width: '120px',
      biddingData: [],
      status: PP_STATUS,
      params: {
        goodName: '',
        goodId: '',
        dealMinMoney: '',
        dealMaxMoney: '',
        sellMinMoney: '',
        sellMaxMoney: '',
        status: ''
      },
      currentPage: 1,
      total: 0,
      loading: false
    }
  },
  created() {
    // 初始化
    this.getList()
  },
  methods: {
    getList() {
      this.loading = true
      getBiddingList(this.params, this.currentPage)
        .then(
          res => {
            this.total = res.paged.total
            this.biddingData = res.list
          },
          err => {
            this.$message.error(err.errorMsg)
          }
        )
        .finally(() => {
          this.loading = false
        })
    },
    searchBid() {
      this.getList()
    },
    clearParams() {
      for (let key in this.params) {
        this.params[key] = ''
      }
    },
    goEnroll(id) {
      this.$router.push({ name: 'EnrollRecord', query: { id: id } })
    },
    goOffer(id) {
      this.$router.push({ name: 'OfferRecord', query: { id: id } })
    },
    handleCurrentChange() {
      this.getList()
    },
    fmtStatus(row, column, cellValue) {
      let s
      this.status.forEach(item => {
        if (item.id == cellValue) {
          s = item.name
        }
      })
      return s
    }
  }
}
</script>

<style lang="scss" scoped>
.container {
  & > div {
    margin-top: 20px;
  }
  .list-pagination-wrapper {
    text-align: center;
  }
}
</style>

<style lang="scss">
.bid-unique-search-wrapper {
  .el-form-item__label {
    text-align: center;
  }
}
</style>
